/*引入atom的代码高亮样式文件*/
.mdContainer {
  width: 100%;
  height: 100%;
  background: lightblue;
}
.mdContainer.fullPage {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
}
.mdContainer .navContainer {
    width: 100%;
    height: 36px;
    background: #fff;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0 10px;
}
.mdContainer .navContainer .nameContainer {
      color: lightblue;
      margin-right: 10px;
      cursor: pointer;
}
.mdContainer .navContainer .markContainer {
      width: auto;
      height: 100%;
      margin-left: 0px;
}
.mdContainer .navContainer .markContainer ul.markListGroup {
        height: 100%;
        width: auto;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: flex-start;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.mdContainer .navContainer .markContainer ul.markListGroup li.markListItem {
          list-style: none;
          width: 20px;
          height: 20px;
          margin: 0 2px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: center;
              -ms-flex-pack: center;
                  justify-content: center;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          cursor: pointer;
          font-size: 12px;
          color: #333;
}
.mdContainer .navContainer .markContainer ul.markListGroup li.markListItem:hover {
            background: #eee;
}
.mdContainer .mdBodyContainer {
    width: 100%;
    height: calc(100% - 36px);
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    box-sizing: border-box;
}
.mdContainer .mdBodyContainer.noMenu {
      height: 100%;
}
.editContainer {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  border-right: 1px solid #ddd;
  background: #333;
  color: #fff;
  padding: 10px;
}
.editContainer .mdEditor {
    height: 100%;
    width: 100%;
    background: transparent;
    outline: none;
    color: #fff;
    resize: none;
}
.previewContainer {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background: #fff;
  overflow: auto;
  padding: 10px;
}



